.App {
  min-height: 100vh; }
  @media (min-width: 992px) {
    .App {
      background: linear-gradient(90deg, #fbfbfb 0%, #fbfbfb 60%, #fff 60%); } }

.header {
  position: sticky;
  max-height: 80px; }

.body {
  max-width: 1200px;
  padding: 0 32px;
  margin: auto; }
  .body_main {
    background: #fbfbfb; }
  .body_aside {
    background: #fff; }
  @media (min-width: 992px) {
    .body {
      display: flex;
      min-height: calc(100vh - 80px); }
      .body_main, .body_aside {
        display: inline-flex;
        padding: 48px;
        min-height: 100%; }
      .body_main {
        width: calc(100% - 400px); }
      .body_aside {
        width: 400px;
        border-left: 2px solid #e0e0e0; } }
