.panel {
  position: relative;
  box-sizing: border-box;
  width: 100%; }

.section {
  text-align: left;
  background: #fff;
  border: solid 1px #e0e0e0;
  border-bottom-width: 0;
  padding: 14px 10px; }
  .section__theme_dark {
    background: #fbfbfb; }
  .section:first-child {
    border-radius: 4px 4px 0 0; }
  .section:last-child {
    border-radius: 0 0 4px 4px;
    border-bottom-width: 1px; }
