.header {
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  background-color: #213945; }
  .header_content {
    width: 100%;
    box-sizing: content-box;
    max-width: 1200px;
    padding: 0 32px;
    margin: auto;
    text-align: left; }
  .header_logo {
    height: 80px;
    will-change: height;
    -webkit-transition: height ease-out 1s;
    transition: height ease-out 1s;
    -webkit-transition-delay: 0.4s;
    transition-delay: 0.4s; }
  .header__small .header_logo {
    height: 48px; }
